.block-pushes {
    .items {
        @include make-row();
        --bs-gutter-y: #{$grid-gutter-width};
        > div {
            @include make-col-ready(); }

        &-2 {
            @include media-breakpoint-up(lg) {
                > div {
                    @include make-col(6); } }
            h2, h3 {
                @include font-size(map-get($display-font-sizes, 6)); }
            .content > div {
                @include media-breakpoint-up(lg) {
                    width: 100%;
                    h2, h3 {
                        font-size: calc(#{$font-size-base} * 1.5); } }
                @include media-breakpoint-up(xl) {
                    width: 60%; } }
            @include media-breakpoint-up(lg) {
                .push {
                    --bs-aspect-ratio: calc(9 / 16 * 100%); } } }

        &-3 {
            @include media-breakpoint-up(lg) {
                > div {
                    @include make-col(4); } } } }

    @include media-breakpoint-down(md) {
        .carousel {
            .push {
                margin-left: 0;
                margin-right: 0;
                width: 100%; } } } }

