.timeline {
    list-style-type: none;
    margin-bottom: 0;
    padding-left: 0;
    position: relative;
    &::before {
        background-color: $secondary;
        content: '';
        height: 100%;
        left: .3rem;
        position: absolute;
        width: 1px; }
    li {
        padding-left: 2rem;
        position: relative;
        &::before {
            background-color: $secondary;
            border-radius: 50%;
            content: '';
            height: .75rem;
            left: 0;
            position: absolute;
            width: .75rem; }
        &:not(:last-child) {
            @include margin-bottom($spacer * 2); } }
    p {
        margin-bottom: 0; }
    .surtitle {
        color: $secondary; }
    .title {
        @include font-size($h3-font-size);
        font-family: $headings-font-family; } }
