.mini-card {
    align-items: center;
    display: flex;
    line-height: 1.4;
    position: relative;
    picture,
    .avatar {
        flex-shrink: 0;
        margin-right: 1rem; }
    .avatar {
        background-color: $light; }
    span {
        @include font-size($small-font-size);
        color: $text-muted; }
    a {
        @extend .stretched-link; } }
