.main-menu {
    --bs-offcanvas-bg: #{$menu-background-color};
    @extend .offcanvas;
    @extend .offcanvas-end;
    .header {
        @extend .offcanvas-header;
        .title {
            @extend .offcanvas-title; }
        button {
            @extend .btn-close;
            background-image: escape-svg($btn-close-bg);
            background-position: center;
            background-repeat: no-repeat;
            background-size: 1rem auto; } }
    .content {
        @extend .offcanvas-body;
        @include media-breakpoint-up(lg) {
            align-items: baseline; } }

    .nav-primary {
        align-items: baseline;
        margin-left: auto; }
    .nav-link {
        --bs-nav-link-color: #{$header-color};
        &:hover {
            --bs-nav-link-hover-color: #{$header-color}; } }
    .dropdown > span {
        --bs-nav-link-color: #{$header-color}; }
    .dropdown-toggle {
        &::after {
            border-top-color: $header-color; } }
    .nav-item-highlighted {
        a {
            --bs-btn-padding-y: #{$nav-link-padding-y};
            @extend .btn;
            @extend .btn-primary;
            margin-left: $nav-link-padding-x;
            margin-right: $nav-link-padding-x; }
        @include media-breakpoint-down(md) {
            margin-top: $nav-link-padding-y; } }


    @include media-breakpoint-down(lg) {
        .nav {
            align-items: center;
            flex-direction: column;
            text-align: center;
            li {
                font-size: px2rem(20); }
            ul li {
                font-size: px2rem(16); } }
        .nav + .nav {
            margin-top: $spacer; } }

    @include media-breakpoint-up(lg) {
        .nav:last-child {
            margin-right: - $nav-link-padding-x; } } }
