.quote {
    blockquote {
        margin-bottom: 0;
        text-align: center;
        text-wrap: balance; }
    blockquote + figcaption {
        align-items: center;
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-bottom: 0;
        &::before {
            content: none; }
        picture {
            display: block; }
        div {
            line-height: 1.2;
            text-align: center; }
        picture + div {
            text-align: left; }
        cite {
            font-style: normal;
            font-weight: $font-weight-bold;
            & ~ a {
                color: $blockquote-footer-color; } }
        & + div {
            margin-top: 2rem; } } }
