.block-form {
    form {
        @include make-row();
        > * {
            @include make-col-ready();
            @include media-breakpoint-up(lg) {
                @include make-col(6); } }
        > div:not(:last-child) {
            margin-bottom: $spacer * 2; }
        .full,
        .actions {
            @include media-breakpoint-up(lg) {
                @include make-col(12); } } } }
