.metas {
    @include font-size($small-font-size);
    p {
        margin-bottom: 0; }
    @include media-breakpoint-up(md) {
        align-items: baseline;
        display: flex;
        > * {
            &:not(:first-child):not(.share) {
                margin-left: .5rem;
                &::before {
                    content: '';
                    margin-right: .5rem; } } } }
    .share {
        margin-left: auto;
        button {
            @extend .btn-sm;
            @extend .btn-link; } } }
