main {
    @include padding-bottom($global-spacing-double);
    @include padding-top($global-spacing);
    outline: 0;

    .row > {
        .content {
            @extend .col-md-7; }
        .sidebar {
            @extend .col-md-4;
            @extend .offset-md-1; }
        @include media-breakpoint-down(md) {
            header {
                order: 1; }
            .content {
                order: 3; }
            .media {
                margin-bottom: $grid-gutter-width;
                order: 2; }
            .sidebar {
                margin-bottom: $grid-gutter-width;
                order: 2; }
            nav {
                order: 4; } } }

    // Offset for anchor with sticky header
    [id] {
        scroll-margin-top: $offset; } }

