.splide {
    .splide {
        &__list {
            display: flex;
            list-style: none;
            margin-bottom: 0;
            padding-left: 0; }
        &__slide {
            flex-shrink: 0; }
        &__pagination {
            &__page {
                &.is-active {
                    background: $primary; } } } } }
