.share {
    @extend .dropdown;
    @include media-breakpoint-down(md) {
        display: none; }
    > button {
        @extend .btn-light;
        @extend .dropdown-toggle;
        @if variable-exists(icons-map) {
            &::before {
                @include icon("share");
                font-size: 1rem;
                line-height: 1;
                margin-right: .5rem;
                margin-top: -.1rem;
                vertical-align: middle; } } }
    ul {
        @extend .dropdown-menu; }
    li {
        a {
            @extend .dropdown-item; } } }
